import React from 'react';
import reactDom from "react-dom";
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import Home from "./view/home/home";
import Course from "./view/course/course"
import Contact from "./view/contact/contact";
import Blog from "./view/blog/blog";
import Job from "./view/job/job";

import "./assets/css/bootstrap.min.css";
import "./assets/css/font-awesome.min.css"
import "./assets/css/animate.css";
import "./assets/css/owl.carousel.css";
// import "./assets/css/slick.css";
import "./assets/css/off-canvas.css";
import "./assets/fonts/linea-fonts.css";
import "./assets/fonts/flaticon.css";
import "./assets/css/magnific-popup.css";
import "./assets/css/rsmenu-main.css";
import "./assets/css/rs-spacing.css";
import "./style.css";
import "./assets/css/responsive.css";

class App extends React.Component {


    render() {
        return (

            <Switch>

                <Route exact path="/" component={Home} />
                <Route exact path="/course" component={Course} />
                <Route exact path="/contact" component={Contact} />
                <Route exact path="/blog" component={Blog} />
                <Route exact path="/job" component={Job} />

            </Switch>
        )
    }
}

reactDom.render(<BrowserRouter basename="react-test">
    <App />
</BrowserRouter>, document.getElementById("root"));
